import React from 'react'
import './ReviewHistory.scss'
import ReviewExpander from './ReviewExpander'
import ReviewText from './ReviewText'
import Accordion from '@material-ui/core/Accordion'
import AccordionSummary from '@material-ui/core/AccordionSummary'
import AccordionDetails from '@material-ui/core/AccordionDetails'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'

const ReviewHistory = ({ process, type }) => {

  const reviewList = (type==="reviews") ? process : {reviews: [process]}

  console.log(reviewList)

  return (
    <section className="rc-review-history__wrapper">
      <h3>{ type==="reviews" ? 'Review History' : 'Response'}</h3>
      { reviewList.reviews.map( (review, index) => (
        <Accordion key={"accordion-"+index} TransitionProps={{ unmountOnExit: true }}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls={"panel-content-" + index}
          id={"panel-header-" + index}
        ><ReviewExpander date={review.posting_date} reviewer={review.reviewed_by} review_id={review.review_idx} type={type}></ReviewExpander></AccordionSummary>
        <AccordionDetails>
          <ReviewText text={review.text}></ReviewText>
        </AccordionDetails>
        </Accordion>
      ))}
    </section>
  )
}

export default ReviewHistory