import React from 'react'

const Author = ({details}) => {

  const corresponding = (isCorresponding) => (isCorresponding==='yes' ? '*' : '')

  const authorLink = (details.orcid) ? (
    <span className="rc-author"><a href={details.orcid} target="_blank" rel="nofollow,noreferrer">
      {details.given_names} {details.surname}
    </a>{corresponding(details.corresp)}</span>
  ) : (
    <span className="rc-author">{details.given_names} {details.surname}{corresponding()}</span>
  );

  return authorLink
}

export default Author