import React, { useState } from 'react'
import './Abstract.scss'

const Abstract = ({ text }) => {

  let [expanded, setExpanded] = useState(false)

  const expanderClass = () => (expanded) ? ' rc-abstract__expander--expanded' : ' rc-abstract__expander--collapsed'

  const toggleExpander = () => {
    setExpanded(!expanded)
  }

  return (
    <div className="rc-abstract">
      <h3 className="rc-abstract__title">Abstract</h3>
      <div className={"rc-abstract__content" + expanderClass()}>{text}
      <div className="rc-abstract__expand-toggle-wrapper">
        <button onClick={toggleExpander} className="rc-abstract__expand-toggle">{(expanded) ? 'Collapse' : 'Read more...'}</button>
      </div>
      </div>
    </div>
  )
}

export default Abstract