import React from 'react'
import './Paper.scss'
import Author from '../Author/Author'
import Abstract from '../Abstract/Abstract'
import ReviewHistory from '../ReviewHistory/ReviewHistory'

const Paper = ({ record }) => {

  const publicationDate = new Date(record.pub_date).toLocaleDateString('en-GB', {
    day: 'numeric',
    month: 'long',
    year: 'numeric'
  })

  const showReviews = () => (record.review_process.reviews.length > 0) ? (<ReviewHistory process={record.review_process} type="reviews"></ReviewHistory>) : null

  const showResponse = () => (record.review_process.response) ? (<ReviewHistory process={record.review_process.response} type="response"></ReviewHistory>) : null

  return (
    <article className="rc-paper">
      <header className="rc-paper__info-header">
        <div className="rc-paper__info-header-left">
          <div className="rc-paper__info-journal">
          <a className="rc-paper__info-journal-link" href={'https://doi.org/' + record.doi} rel="nofollow,noreferrer" target="_blank">{record.journal}</a>
          </div>
          <div className="rc-paper__info-date">
            {publicationDate}
          </div>
        </div>
        <div className="rc-paper__info-doi rc-paper__info-header-right">
          doi: <a href={'https://doi.org/' + record.doi} rel="nofollow,noreferrer" target="_blank">{record.doi}</a>
        </div>
      </header>
      <h3 className="rc-paper__title">{record.title}</h3>
      <ul className="rc-paper__author-list">
        {
          record.authors.map( (author, index) => (
            <li className="rc-paper__author-list-item" key={index}>
              <Author details={author}></Author>
            </li>
          ))
        }
      </ul>
      <div className="rc-paper__corresponding-label">
        * corresponding author(s)
      </div>
      <Abstract text={record.abstract}></Abstract>
      {showReviews()}
      {showResponse()}
    </article>
  )
}

export default Paper;