/*
 check that browser has localStorage
 if it does, you can use the "storage" shortcut
 to refer to it.
 */
let storage;
let fail;
let uid;
try {
	uid = new Date;
	(storage = window.localStorage).setItem(uid, uid);
	fail = storage.getItem(uid) != uid;
	storage.removeItem(uid);
	fail && (storage = false);
} catch (exception) {}

/*
Stash the data in localStorage, replacing existing data if it's expired
*/



const stashDois = {

  storageId: 'rcdoilist',

  get() {

    const now = new Date();

    if (!storage) return false

    const doiList = storage.getItem(this.storageId)

    if (!doiList) return false

    const doiData = JSON.parse(doiList)

    if (new Date(doiData.expiryDate) < now) {

      storage.removeItem(this.storageId)

      return false

    }

    return doiData.data

  },

  set(doiList) {

    if (!storage) return false

    const now = new Date();
    const expiry = new Date(now.getTime())
    // cache expires six hours in the future 6 * 60 *
    expiry.setTime(expiry.getTime() + (6 * 60 * 60 * 1000))

    const toStore = {
      expiryDate: expiry.getTime(),
      data: doiList
    }

    storage.setItem(this.storageId, JSON.stringify(toStore))

    return doiList

  }

}

export default stashDois