import React, { useEffect, useState } from 'react'
import Paper from './components/Paper/Paper'
import { get, post } from './services/http'
import stashDois from './services/stashDois'
import './App.scss'

const App = () => {

  let [papers, setPapers] = useState([])

  let getDoiData = async (dois) => {
    post('dois/', {
      dois
    }).then(response => {
      console.log(response)
      setPapers(response)
    })
  }

  useEffect(() => {

    let dois = stashDois.get()

    if(!dois) {

      get('by_reviewing_service/').then(response => {
        let rcdata = (response.filter( source => (source.id === 'review commons')))[0]
        dois = rcdata.papers.reduce((accumulator, paper) => {
          accumulator.push(paper.doi)
          return accumulator
        }, [])

        stashDois.set(dois)
        getDoiData(dois)
       })

    } else {
      getDoiData(dois)
    }

  }, [])

  return (
    <ul className="rc-list-container">
      {
        papers.map( paper => (
          <li className="rc-list-item" key={paper.id}><Paper record={paper}></Paper></li>
        ))
      }
    </ul>
  )

}


export default App